@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
}

.App {
  text-align: center;
  min-height: 100vh;
  min-width: 100vh;
  /* display: flex;
  justify-content: center;
  align-items: center; */
}
.container{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
}
.NewTask{
  width: 40%;
  min-height: 100%;
  background: #4ac284;
  padding: 30px 20px;
}

.ListTask{
  width: 60%;
  min-height: 100%;
  border-left: 1px solid black;
}